import React from 'react';
import styled from 'styled-components';
import ExternalLink from '@ui/components/ExternalLink';
import DownloadIcon from '@ui/components/icons/DownloadIcon';

export type InlineDownloadProps = React.HTMLAttributes<HTMLDivElement> & {
  downloadTitle?: string,
  downloadUrl: string,
}

const Container = styled.div`
  background-color: #ECF3FA;
  padding: 2.125rem 2.8125rem;
  width: auto;
  margin: 1.25rem;
  border-radius: 0.4375rem;
  &:first-child {
    margin-top: 0.625rem;
  }
  &:last-child {
    margin-bottom: 0.625rem;
  }
`

const DownloadTitle = styled.div`
  font-size: ${p => p.theme.fontSizes.medium2x};
  font-weight: 900;
  color: #012F57;
  display: block;
  margin-bottom: 0.75rem;
`

const DownloadLink = styled(ExternalLink)`
  color: #00AFD9;
  text-decoration: underline;
  font-size: ${p => p.theme.fontSizes.small2x};
  display: flex;
  align-items: center;
`

const DownloadIconStyled = styled(DownloadIcon)`
  margin-right: 0.625rem;
  font-size: ${p => p.theme.fontSizes.medium};
`

export const InlineDownload = ({downloadUrl, downloadTitle, ...rest}: InlineDownloadProps) => (
  <Container {...rest}>
    {downloadTitle && <DownloadTitle>{downloadTitle}</DownloadTitle>}
    <DownloadLink href={downloadUrl} target="_blank">
      <DownloadIconStyled/>
      Download
    </DownloadLink>
  </Container>
)

export default InlineDownload;