import React from 'react'
import styled from 'styled-components'
import { media } from 'styled-bootstrap-grid'
import withErrorBoundary from '../../shared/ErrorBoundary'

export type SpacerProps = {
  lineBreak?: boolean,
}

const PaddingSpace = styled.div`
  ${media.xs`
    padding-bottom: 1.875rem;
  `}
  ${media.sm`
    padding-bottom: 1.875rem;
  `}
  ${media.md`
    padding-bottom: 3.75rem;
  `}
`

const Hr = styled.hr`
  border: 1px solid #08344e80;
  opacity: 0.28;
  margin-bottom: 0px;
  background: none;
  height: 0px;
`

export const Spacer = ({ lineBreak }: SpacerProps) => {
  return (
    <>
      {lineBreak && (
        <>
          <PaddingSpace />
          <Hr />
        </>
      )}
      <PaddingSpace />
    </>
  )
}

export default withErrorBoundary(Spacer)
