import React from 'react'
import styled from 'styled-components'
import { media } from 'styled-bootstrap-grid'
import withErrorBoundary from '@ui/shared/ErrorBoundary'

export type HeadingProps = {
  headingText: string,
  centred?: boolean,
  headingType: 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'h7' | 'eyebrow',
  backgroundColor?: string,
}

const H7 = styled.span`
  color: ${props => props.theme.heading.eyebrowColor};
  display: block;
  margin-bottom: 1.45rem;
  font-weight: 900;
  line-height: 1.5;
  ${media.xs`
    font-size: 0.75rem;
  `}
  ${media.sm`
    font-size: 1.6rem;
  `}
`

const Eyebrow = styled(H7)`
  text-transform: uppercase;
`

const Container = styled.div`
  color: ${props => props.theme.heading.color};
`

const UnstyledHeading = ({ headingType, headingText }: HeadingProps) => {
  if (!['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'h7', 'eyebrow'].includes(headingType)) {
    return null
  }

  if (!headingText) {
    return null
  }

  if (headingType == 'h7') {
    return <H7>{headingText}</H7>
  }

  if (headingType == 'eyebrow') {
    return <Eyebrow>{headingText}</Eyebrow>
  }

  return <Container as={headingType}>{headingText}</Container>
}

export const Heading = styled(UnstyledHeading)`
  text-align: ${props => props.centred && 'center' || 'left'};
  ${props => props.backgroundColor && `background-color: ${props.backgroundColor};`};
`

export default withErrorBoundary(Heading)
