import { excludeProps } from '@app/ui/utils/shouldForwardProp';
import React from 'react';
import { Col, media, Row } from 'styled-bootstrap-grid';
import styled from 'styled-components';

export type TwoColumnRowProps = {
  column1: {
    html?: string,
    children?: React.ReactNode,
  },
  column2: {
    html?: string,
    children?: React.ReactNode,
  },
  wideColumnSide?: 'left' | 'right',
  backgroundColor?: string,
}


const Container = styled('div')
.withConfig<{
  reversed?: boolean
}>({
  shouldForwardProp: excludeProps(['reversed'])
})`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  align-items: center;
  margin-bottom: 1rem;
  ${media.max.sm`
    flex-wrap: ${(p: any) => p.reversed ? 'wrap-reverse' : 'wrap'};
  `}
`

const Column = styled('div')
.withConfig<{ wide?: boolean, width?: string, backgroundColor?: string }>({
  shouldForwardProp: excludeProps(['wide', 'width', 'backgroundColor'])
})`
  flex: ${p => p.width};
  min-width: ${(p: any) => p.wide ? '19.125rem' : '12.5rem'};
  ${media.max.tablet`
    flex: 1;
    min-width: 100%;
    width: 100%;
    &:last-child {
      margin-left: 0;
    }
  `}
  ${media.md`
    &:last-child {
      margin-left: 2.5rem;
    }
  `}

  .title {
    font-size: ${p => p.theme.fontSizes.normal};
    color: #012F57;
    font-weight: 700;
  }
  p {
    font-size: ${p => p.theme.fontSizes.normal};
  }
`

export const TwoColumnRow = ({wideColumnSide = 'right', column1, column2, backgroundColor}: TwoColumnRowProps) => {
  const isLeft = wideColumnSide === 'left';
  
  const props1: any = {
    wide: isLeft,
    width: isLeft && '0.66' || '0.33',
    backgroundColor,
  };

  if (column1.children) {
    props1.children = column1.children
  } else if (column1.html) {
    props1.dangerouslySetInnerHTML = { __html: column1.html || '' }
  }

  const props2: any = {
    wide: !isLeft,
    width: !isLeft && '0.66' || '0.33',
    backgroundColor,
  };
  if (column2.children) {
    props2.children = column2.children
  } else if (column2.html) {
    props2.dangerouslySetInnerHTML = { __html: column2.html || '' }
  }

  return (
    <Container
      reversed={wideColumnSide === 'right'}
    >
      <Column
        {...props1}
      />
      <Column
        {...props2}
      />
    </Container>
  );
}

export default TwoColumnRow;