import React, { ImgHTMLAttributes } from 'react';
import styled from 'styled-components';
import withErrorBoundary from '@ui/shared/ErrorBoundary';
import { excludeProps } from '@ui/utils/shouldForwardProp';

export type ImageProps = ImgHTMLAttributes<any> & {
  width?: number,
  height?: number,
  src: string,
  radius?: string,
  caption?: string
  noGutter?: boolean
}

const Container = styled('figure')
.withConfig<{
  radius?: string,
  noGutter?: boolean
}>({
  shouldForwardProp: excludeProps(['radius'])
})
`
  border-radius: ${p => p.radius};
  position: relative;
  display: inline-flex;
  margin-bottom: ${p => p.noGutter && '0' || 'initial'};
`

const Img = styled('img')
.withConfig<{
  radius?: string
}>({
  shouldForwardProp: excludeProps(['radius'])
})`
  border-radius: ${p => p.radius};
`

const Caption = styled('figcaption')
.withConfig<{
  radius?: string
}>({
  shouldForwardProp: excludeProps(['radius'])
})`
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  bottom: 0;
  border-bottom-left-radius: ${p => p.radius};
  border-bottom-right-radius: ${p => p.radius};
  background-color: rgba(1,47,87,0.65);
  height: 20%;
  display: flex;
  align-items: center;
  color: white;
  max-height: 3.4375rem;
`

const CaptionText = styled.span`
  margin-left: 2.82%;
`

export class Image extends React.PureComponent<ImageProps> {
  img: HTMLImageElement | null = null;
  static defaultProps = {
    radius: '0.625rem'
  }
  state = {
    loaded: false,
  }
  componentDidMount() {
    this.loadImage();
    window.addEventListener('scroll', this.loadImage);
  }

  componentWillUnmount() {
    this.removeEvent();
  }

  removeEvent() {
    window.removeEventListener('scroll', this.loadImage);
  }

  elementInViewport(el: HTMLImageElement) {
    const rect = el.getBoundingClientRect();
    return (
      rect.top >= 0
      && rect.left >= 0
      && rect.top <= (window.innerHeight || document.documentElement.clientHeight)
    )
  }

  loadImage = () => {
    if (this.img && this.elementInViewport(this.img)) {
      this.img.onload = this.onLoaded;
      this.img.src = this.props.src;
    }
  }

  onLoaded = () => {
    this.removeEvent();
    this.setState({loaded: true});
  }
  render() {
    const {
      loaded
    } = this.state;

    const { 
      width, 
      height,
      radius,
      src,
      caption,
      alt,
      ...rest 
    } = this.props;
    return (
      <Container
        radius={radius}
      >
        <Img
          ref={(ref) => this.img = ref}
          radius={radius}
          title={caption || alt}
          alt={caption || alt}
          {...rest}
        />
        {loaded && caption && <Caption radius={radius}>
          <CaptionText>{caption}</CaptionText>
        </Caption>}
      </Container>
    );
  }
}

export default withErrorBoundary(Image);
